import { useText } from '@adobe-commerce/elsie/i18n';
import { FunctionComponent } from 'preact';

interface SignInProps {
  onClick: (event: Event) => void;
}

export const SignIn: FunctionComponent<SignInProps> = ({ onClick }) => {
  const translations = useText({
    account: 'Checkout.LoginForm.account',
    signIn: 'Checkout.LoginForm.signIn'
  });

  return (
    <div className="checkout-login-form__sign-in">
      {translations.account}

      <a
        className="checkout-login-form__link"
        data-testid="sign-in-link"
        href="#"
        rel="noreferrer"
        target="_blank"
        onClick={(event) => {
          event.preventDefault();
          onClick(event);
        }}
      >
        {translations.signIn}
      </a>
    </div>
  );
};
