import {
  getCustomer,
  isEmailAvailable,
  setGuestEmailOnCart,
} from '@/checkout/api';
import {
  LoginForm as LoginFormComponent,
  SignIn,
  SignOut,
} from '@/checkout/components/LoginForm';
import { Cart, Customer, NegotiableQuote } from '@/checkout/data/models';
import {
  getCartEmail,
  getLatestCheckoutUpdate,
  getValue,
  notifyValues,
  validateEmail,
} from '@/checkout/lib';
import { TitleProps } from '@/checkout/types';
import { useText } from '@adobe-commerce/elsie/i18n';
import { Container, Slot, SlotProps } from '@adobe-commerce/elsie/lib';
import { events } from '@adobe-commerce/event-bus';
import { HTMLAttributes } from 'preact/compat';
import {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'preact/hooks';

interface ValidationError {
  email: string;
  message: string;
  type: 'missing' | 'invalid';
}

interface CartSyncError {
  email: string;
  error: Error;
}

export interface LoginFormProps
  extends HTMLAttributes<HTMLFormElement>,
    TitleProps {
  active?: boolean;
  autoSync?: boolean;
  displayHeadingContent?: boolean;
  onSignInClick?: (email: string) => void;
  onSignOutClick?: () => void;
  onCartSyncError?: (error: CartSyncError) => void;
  onValidationError?: (error: ValidationError) => void;
  slots?: {
    Heading?: SlotProps<{
      authenticated: boolean;
    }>;
  } & TitleProps['slots'];
}

const DEBOUNCE_TIME = 1000;

export const LoginForm: Container<LoginFormProps> = ({
  active = true,
  autoSync = true,
  displayHeadingContent = true,
  displayTitle = true,
  initialData,
  onCartSyncError,
  onSignInClick,
  onSignOutClick,
  onValidationError,
  slots,
  ...props
}) => {
  const [customer, setCustomer] = useState<Customer | null>(null);
  const [email, setEmail] = useState<string>('');
  const [error, setError] = useState<string>('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isAvailable, setIsAvailable] = useState<boolean>(true);
  const [isInitialized, setIsInitialized] = useState<boolean>(false);

  const debounceTimer = useRef<ReturnType<typeof setTimeout> | null>(null);

  const {
    alreadyHaveAccountHint,
    cartSyncError,
    defaultTitle,
    fasterCheckoutHint,
    invalidEmailError,
    missingEmailError,
    signInLabel,
  } = useText({
    alreadyHaveAccountHint: 'Checkout.LoginForm.emailExists.alreadyHaveAccount',
    cartSyncError: 'Checkout.LoginForm.cartSyncError',
    defaultTitle: 'Checkout.LoginForm.title',
    fasterCheckoutHint: 'Checkout.LoginForm.emailExists.forFasterCheckout',
    invalidEmailError: 'Checkout.LoginForm.invalidEmailError',
    missingEmailError: 'Checkout.LoginForm.missingEmailError',
    signInLabel: 'Checkout.LoginForm.emailExists.signInButton',
  });

  const setEmailAndCheckAvailability = useCallback(
    (email: string) => {
      if (!validateEmail(email) || email === getCartEmail()) return;

      isEmailAvailable(email)
        .then((availability) => {
          setIsAvailable(availability);
        })
        .catch((error) => {
          console.error(error);
          setIsAvailable(true);
        });

      if (!autoSync) return;

      setGuestEmailOnCart(email).catch((error) => {
        onCartSyncError?.({ email, error });

        if (!onCartSyncError) {
          setError(cartSyncError);
        }
      });
    },
    [autoSync, onCartSyncError, cartSyncError]
  );

  const handleChange = useCallback(
    (event: Event) => {
      const target = event.target as HTMLInputElement;
      const email = target.value;

      setEmail(email);
      setIsAvailable(true);
      setError('');

      if (debounceTimer.current) {
        clearTimeout(debounceTimer.current);
      }

      debounceTimer.current = setTimeout(() => {
        setEmailAndCheckAvailability(email);
        notifyValues({ email });
        debounceTimer.current = null;
      }, DEBOUNCE_TIME);
    },
    [setEmailAndCheckAvailability]
  );

  const handleBlur = useCallback(
    (event: Event) => {
      const target = event.target as HTMLInputElement;
      const email = target.value.trim();
      const isValid = validateEmail(email);

      if (isValid) {
        setError('');
        target.setCustomValidity('');

        if (!debounceTimer.current) return;

        clearTimeout(debounceTimer.current);
        debounceTimer.current = null;
        setEmailAndCheckAvailability(email);
        notifyValues({ email });
        return;
      }

      const type = email === '' ? 'missing' : 'invalid';
      const message =
        type === 'missing' ? missingEmailError : invalidEmailError;

      if (onValidationError) {
        onValidationError({ email, message, type });
        return;
      }

      setError(message);
      target.setCustomValidity(message);
    },
    [
      invalidEmailError,
      missingEmailError,
      onValidationError,
      setEmailAndCheckAvailability,
    ]
  );

  const handleInvalid = useCallback(
    (event: Event) => {
      const target = event.target as HTMLInputElement;
      const validity = target.validity;

      const message = validity.valueMissing
        ? missingEmailError
        : invalidEmailError;

      if (!onValidationError) {
        setError(message);
        return;
      }

      onValidationError({
        email: target.value,
        message,
        type: validity.valueMissing ? 'missing' : 'invalid',
      });
    },
    [missingEmailError, invalidEmailError, onValidationError]
  );

  const handleSignIn = useCallback(() => {
    const email = getValue('email') ?? '';
    const isValid = validateEmail(email);
    onSignInClick?.(isValid ? email : '');
  }, [onSignInClick]);

  const handleSignOut = useCallback(() => {
    onSignOutClick?.();
  }, [onSignOutClick]);

  const handleCheckoutData = useCallback((data: Cart | NegotiableQuote | null) => {
    const prevEmail = getValue('email') ?? '';
    const email = data?.email ?? '';

    if (email !== prevEmail) {
      setEmail(email);
      setError('');
      setIsAvailable(true);
      notifyValues({ email });
    }
  }, []);

  useEffect(() => {
    if (!active) return;

    const onAuthenticated = events.on(
      'authenticated',
      (authenticated) => {
        setIsAuthenticated(authenticated);

        getCustomer()
          .then((customer) => {
            setCustomer(customer);
          })
          .catch(console.error);
      },
      { eager: true }
    );

    return () => {
      onAuthenticated?.off();
    };
  }, [active]);

  useEffect(() => {
    if (!active) return;

    const pastUpdate = getLatestCheckoutUpdate();

    if (pastUpdate) {
      setIsInitialized(true);
      // When component becomes active, restore local state from checkout data
      const checkoutEmail = pastUpdate.email ?? '';
      if (checkoutEmail) {
        setEmail(checkoutEmail);
      }
      handleCheckoutData(pastUpdate);
      return;
    }

    const onCheckoutInit = events.on(
      'checkout/initialized',
      (data) => {
        setIsInitialized(true);
        handleCheckoutData(data);
      },
      { eager: true }
    );

    return () => {
      onCheckoutInit?.off();
    };
  }, [active, handleCheckoutData]);

  useEffect(() => {
    if (!active) return;

    const onCheckoutUpdated = events.on(
      'checkout/updated',
      handleCheckoutData,
      { eager: false }
    );

    return () => {
      onCheckoutUpdated?.off();
    };
  }, [active, handleCheckoutData]);

  const titleContent = useMemo(() => {
    if (!displayTitle) return undefined;

    return (
      <Slot name="checkout-login-form-title" slot={slots?.Title}>
        <h2>{defaultTitle}</h2>
      </Slot>
    );
  }, [displayTitle, slots, defaultTitle]);

  const headingContent = useMemo(() => {
    if (!displayHeadingContent) return undefined;

    return (
      <Slot
        context={{ authenticated: isAuthenticated }}
        name="checkout-login-form-heading-label"
        slot={slots?.Heading}
      >
        {isAuthenticated ? (
          <SignOut onClick={handleSignOut} />
        ) : (
          <SignIn onClick={handleSignIn} />
        )}
      </Slot>
    );
  }, [
    displayHeadingContent,
    isAuthenticated,
    slots,
    handleSignIn,
    handleSignOut,
  ]);

  const hintContent = useMemo(() => {
    if (isAvailable) return '';

    return (
      <>
        {alreadyHaveAccountHint}{' '}
        <a href="#" onClick={handleSignIn}>
          {signInLabel}
        </a>{' '}
        {fasterCheckoutHint}
      </>
    );
  }, [
    isAvailable,
    alreadyHaveAccountHint,
    signInLabel,
    fasterCheckoutHint,
    handleSignIn,
  ]);

  return (
    <LoginFormComponent
      {...props}
      customer={customer}
      email={email}
      error={error}
      headingContent={headingContent}
      hint={hintContent}
      initialized={isInitialized}
      title={titleContent}
      visible={active}
      onEmailBlur={handleBlur}
      onEmailChange={handleChange}
      onEmailInvalid={handleInvalid}
    />
  );
};
