import { useText } from '@adobe-commerce/elsie/i18n';
import { FunctionComponent } from 'preact';

interface SignOutProps {
  onClick: (event: Event) => void;
}

export const SignOut: FunctionComponent<SignOutProps> = ({ onClick }) => {
  const translations = useText({
    switch: 'Checkout.LoginForm.switch',
    signOut: 'Checkout.LoginForm.signOut'
  });

  return (
    <p className="checkout-login-form__sign-out">
      {translations.switch}

      <a
        className="checkout-login-form__link"
        href="#"
        rel="noreferrer"
        target="_blank"
        onClick={(event) => {
          event.preventDefault();
          onClick?.(event);
        }}
      >
        {translations.signOut}
      </a>
    </p>
  );
};
