import { Email } from '@/checkout/components';
import { WithConditionals } from '@/checkout/components/ConditionalWrapper/ConditionalWrapper';
import '@/checkout/components/LoginForm/LoginForm.css';
import { LoginFormSkeleton } from '@/checkout/components/LoginForm/LoginFormSkeleton';
import { Customer } from '@/checkout/data/models';
import { classes, VComponent } from '@adobe-commerce/elsie/lib';
import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes } from 'preact/compat';

export interface LoginFormProps
  extends Omit<HTMLAttributes<HTMLFormElement>, 'title'> {
  customer: Customer | null;
  email: string;
  error: string;
  headingContent?: VNode;
  hint: string | VNode;
  onEmailBlur: (event: Event) => void;
  onEmailChange: (event: Event) => void;
  onEmailInvalid: (event: Event) => void;
  title?: VNode;
}

const LoginFormComponent: FunctionComponent<LoginFormProps> = ({
  className,
  customer,
  email,
  error,
  headingContent,
  hint,
  name,
  onEmailBlur,
  onEmailChange,
  onEmailInvalid,
  title,
  ...props
}) => {
  return (
    <div className="checkout-login-form" data-testid="checkout-login-form">
      <div className="checkout-login-form__heading">
        {title && (
          <VComponent className="checkout-login-form__title" node={title} />
        )}

        {headingContent && (
          <VComponent
            className="checkout-login-form__heading-label"
            node={headingContent}
          />
        )}
      </div>

      {customer ? (
        <div className="checkout-login-form__customer-details">
          <div className="checkout-login-form__customer-name">
            {`${customer.firstName} ${customer.lastName}`}
          </div>

          <div className="checkout-login-form__customer-email">
            {customer.email}
          </div>
        </div>
      ) : (
        <div className="checkout-login-form__content">
          <form
            {...props}
            noValidate
            className={classes(['dropin-login-form__form', className])}
            name={name}
          >
            {/* Prevent 'Enter' key press from submitting this form. */}
            <button
              disabled
              aria-hidden="true"
              style="display: none"
              type="submit"
            />
            <Email
              error={error}
              hint={hint as any}
              value={email}
              onBlur={onEmailBlur}
              onChange={onEmailChange}
              onInvalid={onEmailInvalid}
            />
          </form>
        </div>
      )}
    </div>
  );
};

export const LoginForm = WithConditionals(
  LoginFormComponent,
  LoginFormSkeleton
);
